body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header
{
  background-color: black;
  color: white;
  padding:20px 70px;
  display:flex;
  justify-content: space-between;

}
.carticon .counter
{
  height:15px;
  width:15px;
  background-color: blue;
  border-radius: 50%;
  position:relative;
  right: 7px;
  top:-3px;
  display:flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}
.carticon
{
  display:flex;
}
.icon{
 font-size:22px;
}
img
{
  height: 150px;
}
.cartwrapper
{
display:inline-flex;
width:320px;
box-shadow:0px 0px 7px black;
}
.imagewrapper{
  width:130px;
  display:flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right:1px solid rgb(129, 126, 126);
  gap: 10px;
}
.content
{
  display:flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 10px;
  padding: 10px;
}
.price
{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity
{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid grey;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity>div:first-child,.quantity>button{
  background-color: rgb(0, 0, 111);
  color:white;
  border-radius: 0;
}
.quantity>button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.total
{
  position:fixed;
  background-color: gray;
  color:black;
  padding:17px 30px;
  bottom:0;
  width:100%;
  box-sizing:border-box;
  text-align:right;
}